import React, { Component } from "react";
import "./style.css";
import link from '../pics/linkedin.png';
import git from '../pics/github.png';
import gmail from '../pics/gmail.png';
class Form extends Component {
  // Setting the component's initial state
  state = {
    firstName: "",
    lastName: "",
    email:"",
    number:""
  };

  handleInputChange = event => {
    // Getting the value and name of the input which triggered the change
    const { name, value } = event.target;

    // Updating the input's state
    this.setState({
      [name]: value
    });
  };

  handleFormSubmit = event => {
    // Preventing the default behavior of the form submit (which is to refresh the page)
    event.preventDefault();
    if(!this.state.firstName||!this.state.lastName||!this.state.email||!this.state.number){
      this.setState({
        firstName: "",
        lastName: "",
        email:"",
        number:""
      });
      return alert(`Please fill out the rest of the form ${this.state.firstName}!`);
    }
    // Alert the user their first and last name, clear `this.state.firstName` and `this.state.lastName`, clearing the inputs
    alert(`Thank You ${this.state.firstName}`);
    this.setState({
      firstName: "",
      lastName: "",
      email:"",
      number:""
    });
  };

  render() {
   
    return (
 
      <div className="container text-white "  style={{ marginTop: `${100}px` }}>
                <b><h2>
          Contact Me! {this.state.firstName} {this.state.lastName}
        </h2></b>
      <div className="row mt-5 ">
        
      <div className="col-md-4">
      <div>


        <a href="https://www.linkedin.com/in/brent-hillen/"><img src={link} alt="Logo" /></a>
        {/* <form className="form">
          <input
            value={this.state.firstName}
            name="firstName"
            onChange={this.handleInputChange}
            type="text"
            placeholder="First Name"
          />
          <input
            value={this.state.lastName}
            name="lastName"
            onChange={this.handleInputChange}
            type="text"
            placeholder="Last Name"
          />
           <input
            value={this.state.lastName}
            name="lastName"
            onChange={this.handleInputChange}
            type="text"
            placeholder="Last Name"
          />
           <input
            value={this.state.email}
            name="email"
            onChange={this.handleInputChange}
            type="text"
            placeholder="Email Address"
          />
          <button onClick={this.handleFormSubmit}>Submit</button>
        </form> */}
      </div>
      </div>
      <div className="col-md-4">
      <a href="https://github.com/bhill7473"><img src={git} alt="Logo" /></a>
      </div>
      <div className="col-md-4">
      <a href="mailto:bhill7473@gmail.com?Subject=Hello%20again"><img src={gmail} alt="Logo" /></a>
      </div>
      </div>

          </div>
    );
  }
}

export default Form;
