import React from "react";
import python from './pics/python.png';
import react from './pics/react.png';
import me from './pics/me.png';

function Jumbotron() {
  return (
    <div className="jumbotron ">
      <div className="row mt-4">
      <div className="col-md-8">
      <h1 class="ml3">Hi there! My name is Brent Hillen.... Welcome to my page!</h1>
      <p>
        I am a web developr and data analyst. I enjoy coding in JavaScript and Python. When I am not at the computer I like to enjoy the nice weather by hiking and fishing. 
      </p>
      </div>
      <img src={me} alt="Logo" />
      </div>
      <div className="row mt-4">
      <div className="col-md-6">
      <h3>Data Skills and Technologies</h3>
      <div className="row mt-4">
      <div className="col-md-3">
      <b><ul>
        <li>Excel</li>
        <li>Tableau</li>
        <li>Python</li>
        <li>Pandas</li>
        <li>Sklearn</li>
        <li>Keras</li>
        <li>D3</li>
      </ul></b>
      </div>
      <div className="col-md-3"> <b><ul>
        <li>SQL</li>
        <li>SQLAlchemy</li>
        <li>MongoDB</li>
        <li>VBA</li>
        <li>Hadoop</li>
        <li>Matplotlib</li>
        <li>Leaflet</li>
      </ul></b>
      </div>
      <div className="col-md-3">
      <img src={python} alt="Logo" />
      </div>
    
      </div>
     
      </div>
      <div className="col-md-6">
      <h3>Web Skills and Technologies</h3>
      <div className="row mt-4">
      <div className="col-md-3">
      <b>
      <ul>
        <li>JavaScript</li>
        <li>Jquery</li>
        <li>React</li>
        <li>HTML</li>
      </ul></b>
      </div>
      <div className="col-md-3"><b> <ul>
        <li>CSS</li>
        <li>Bootstrap</li>
        <li>Node.js</li>
        <li>Express</li>
      </ul></b>
      </div>
      <div className="col-md-3">
      <img src={react} alt="Logo" />
      </div>
      </div>
      </div>
      </div>
    </div>
  );
}

export default Jumbotron;
